.nav-container {
  background: rgba(29, 29, 29, 0.11) !important;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
}
.nav-container .left-container span {
  font-family: "Arial", sans-serif;
  font-size: 24px;
  color: #333;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

.nav-container .right-container span {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  color: #333;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}
.nav-container .right-container {
  display: flex;
  gap: 30px;
  margin-right: 15vw;
}
.nav-container .left-container{
  flex: 1;
  margin-left: 20px;
}
.home{
  min-width: 100%; 
  height: auto; 
}
.home img {
  min-width: 100%; 
  height: auto; 
  display: block;
}